import Data from "@/js/core/data";

const Unsubscribe_Server = "https://api.${region}.500apps.com/bpm/unsubscribe/";

class Unsubscribe extends Data {
  constructor() {
    super(Unsubscribe_Server);
    this.disablePCors();
    this.unsuscribe_url = this.getURL();
  }
  // Unsubscribe the BPM app user
  addToUnsubscribe(domain_id, user_id) {
    this.url = `${this.unsuscribe_url}${domain_id}/${user_id}`;
    var obj = { is_subscribed: "0" };
    return this.add(obj);
  }
}

export default new Unsubscribe();
